/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1a; /* Dark background color */
  color: #fff; /* Text color */
}

/* App Container Styles */
.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #333; /* Dark container background */
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
}

/* Header Styles */
header {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  font-size: 2.5em;
  color: #03a9f4; /* Updated primary color */
}

/* Section Styles */
section {
  text-align: center;
  padding: 20px;
  border: 1px solid #444; /* Border around section */
  border-radius: 10px;
  background-color: #2c2c2c; /* Dark section background */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Increased shadow intensity */
}

p {
  font-size: 1.2em;
  line-height: 1.5;
}

/* Button Styles */
button {
  padding: 15px 30px;
  font-size: 1.2em;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #4caf50; /* Updated success button color */
  color: #fff;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background-color: #388e3c; /* Darker green on hover */
}

/* Footer Styles */
footer {
  text-align: center;
  margin-top: 20px;
  padding: 10px;
  background-color: #2c2c2c; /* Dark footer background */
  color: #fff;
}

/* Responsive Styles */
@media (max-width: 600px) {
  h1 {
    font-size: 2em;
  }

  button {
    padding: 12px 25px;
    font-size: 1em;
  }
}

/* Header Styles */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #007bff; /* Header background color */
  color: #fff; /* Header text color */
}

.logo-container {
  text-align: left;
}

.logo-container h1 {
  margin: 0;
  font-size: 2em;
}

.logo-container p {
  margin: 0;
  font-size: 1.2em;
}

.navigation-menu ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.navigation-menu li {
  margin-right: 20px;
  font-size: 1.2em;
  cursor: pointer;
}

/* Features Styles */
.features {
  padding: 40px;
}

.features h2 {
  font-size: 2.5em;
  color: #03a9f4; /* Updated primary color */
  margin-bottom: 20px;
}

.feature-cards {
  display: flex;
  justify-content: space-around;
}

.feature-card {
  width: 30%;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  margin-bottom: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.feature-card h3 {
  font-size: 1.5em;
  color: #03a9f4; /* Updated primary color */
  margin-bottom: 10px;
}

.feature-card p {
  font-size: 1.2em;
  line-height: 1.5;
}

/* CallToAction Styles */
.cta {
  background-color: #2c2c2c; /* Dark background color */
  padding: 40px;
}

.cta h2 {
  font-size: 2.5em;
  color: #03a9f4; /* Updated primary color */
  margin-bottom: 20px;
}

.cta p {
  font-size: 1.2em;
  line-height: 1.5;
}

.cta-form {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.cta-form input {
  padding: 15px;
  font-size: 1.2em;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-right: 10px;
  width: 250px;
}

.cta-form button {
  padding: 15px 30px;
  font-size: 1.2em;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #4caf50; /* Updated success button color */
  color: #fff;
  transition: background-color 0.3s ease-in-out;
}

.cta-form button:hover {
  background-color: #388e3c; /* Darker green on hover */
}

/* Footer Styles */
.footer {
  padding: 40px;
  background-color: #2c2c2c; /* Dark footer background */
  color: #fff;
}

.footer-content {
  display: flex;
  justify-content: space-between;
}

.footer-logo h1 {
  font-size: 2em;
}

.footer-links ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.footer-links li {
  margin-right: 20px;
  font-size: 1.2em;
  cursor: pointer;
}

.footer-social {
  display: flex;
  flex-direction: column;
}

.social-icons a {
  margin-right: 10px;
  font-size: 1.5em;
  color: #fff;
  text-decoration: none;
}

/* Responsive Styles */
@media (max-width: 600px) {
  .app-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .logo-container {
    text-align: center;
  }

  .navigation-menu {
    margin-top: 20px;
  }

  .navigation-menu ul {
    flex-direction: column;
  }

  .navigation-menu li {
    margin: 10px 0;
    font-size: 1.5em;
  }

  .feature-cards {
    flex-direction: column;
  }

  .feature-card {
    width: 100%;
  }

  .cta-form {
    flex-direction: column;
    align-items: center;
  }

  .cta-form input,
  .cta-form button {
    width: 100%;
    margin-bottom: 10px;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-links {
    margin-top: 20px;
  }

  .footer-links ul {
    flex-direction: column;
    align-items: center;
  }

  .footer-links li {
    margin: 10px 0;
  }

  .footer-social {
    margin-top: 20px;
  }

  .social-icons a {
    font-size: 2em;
  }
}
